import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Analyzes } from '../analyzes';

@Component({
  selector: 'app-analyzes-list',
  templateUrl: './analyzes-list.component.html',
  styleUrls: ['./analyzes-list.component.css']
})
export class AnalyzesListComponent implements OnInit {
  @Input('analyzes')
    analyzes: Analyzes[];
  @Input('viewtype')
    viewType: string = 'full';

  @Output('startmerge')
    startMerge = new EventEmitter<string>();
    
  public currentItem: Analyzes;
  public merge: Analyzes[] = [];  
  public mergeMode: boolean = false;
  constructor() { }

  ngOnInit() {
    
  }

  mergeStart(): void {
    this.mergeMode = true;
    if(this.merge) {
      this.merge.map((item: any) => delete item.merge);
    }
    this.merge = [];
    this.startMerge.emit();
  }

  mergeStop(): void {
    this.mergeMode = false;
  }

  clickItem(item): void {
    this.currentItem = item;
    let deleted = false;
    if(this.mergeMode) {
      let domEl = <any>document.querySelector('#checkField'+item._id);
    
      this.merge.map((mergeItem: any, index) => {
        if(item._id == mergeItem._id) {
          this.merge.splice(index, 1);
          deleted = true;
        }
      });
      
      if(this.merge.length < 3) {
        if(!deleted) this.merge.push(item);
      } else {
        domEl.checked = false;
      }
    }
  }

}



