export interface IAnalyzes {
    _id: string;
    art: {
        helix: string,
        cmd: string,
        invitro: string
    };
    title: {
        helix: string,
        cmd: string,
        invitro: string
    };
    price: {
        helix: string,
        cmd: string,
        invitro: string
    };
    productPrice: {
        helix: number,
        cmd: number,
        invitro: number
    };
    time: {
        helix: string,
        cmd: string,
        invitro: string
    };
    description: any;
    active: boolean;
}

export class Analyzes implements IAnalyzes {
    public _id: string;
    public art: any;
    public title: any;
    public price: any;
    public productPrice: any;
    public time: any;
    public description: any;
    public finishPrice: number;
    public active: boolean;

    constructor(object? :any) {
        this.default();

        if(object != undefined) {
            this.setParams(object);
        }
    }

    setParams(object: any) : void {
        this._id = object._id ? object._id : null;
        if(object.art) this.setLabs(this.art, object.art);
        if(object.title) this.setLabs(this.title, object.title);
        if(object.price) this.setLabs(this.price, object.price);
        if(object.productPrice) this.setLabs(this.productPrice, object.productPrice);
        if(object.time) this.setLabs(this.time, object.time);
        this.description = object.description ? object.description : null;
        this.finishPrice = object.finishPrice ? object.finishPrice : 0;
        this.active = object.active ? true : false;
    }

    clear() : void {
        this.default();
    }

    private setLabs(field, params) {
        field.helix = params.helix ? params.helix : null;
        field.cmd = params.cmd ? params.cmd : null;
        field.invitro = params.invitro ? params.invitro : null;
    }

    private default() : void {
        this._id = null;
		this.art = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.title = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.price = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.productPrice = {
            helix: null,
            cmd: null,
            invitro: null 
        };
        this.time = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.finishPrice = 0;
        this.description = null;
        this.active = true;
    }
}



